<template>
    <div class="agent-report">
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>代理列表</span>
                    <div class="list-operate">
                        <DatePicker v-model="searchDate" type="date" :options="forbiddenTime" format="yyyy-MM-dd" placeholder="选择搜索的时间" class="columns-item-input" @on-change="handleSearch"></DatePicker>
                        <div class="search" @click="handleBackwater">一键回水</div>
                        <div class="poptip">
                            <p>1.代理回水只能一天处理一次。并且只能在06:00后选择前一天回水处理。</p>
                            <p>2.选定时间统计周期：当天06:00 - 次日06:00</p>
                        </div>
                    </div>
                </div>
            </div>
            <div class="list-data">
                <div class="data-header">
                    <p class="item-info">用户名</p>
                    <p class="item-info">流水汇总</p>
                    <p class="item-info">回水汇总</p>
                    <p class="item-info">状态</p>
                    <p class="item-info">操作</p>
                </div>
                <div class="data-con">
                    <div class="data-item" v-for="(item, index) in agentReportData" :key="index">
                        <div class="item-info">{{item.agentName}}</div>
                        <div class="item-info">{{item.allScroll}}</div>
                        <div class="item-info">{{item.backwater}}</div>
                        <div class="item-info">{{item.status}}</div>
                        <div class="item-info operate">
                            <div class="item-button" @click="handleQueryOffline(item)">查看下级数据</div>
                        </div>
                    </div>
                </div>
            </div>
            <Page class="footer-page" :total="page.total" :page-size="page.pageSize" :current="page.pageNum" show-sizer @on-page-size-change="handleChangeSize" @on-change="handleChangePage"/>
        </div>
        <div v-show="offlineData.show" class="offline-data-mask">
            <div v-show="offlineData.show" class="offline-data">
                <div class="offline-data-header">
                    <span>{{offlineData.header}}</span>
                    <span class="modal-close" @click="handleCloseModal">X</span>
                </div>
                <div class="account-setting-list">
                    <i-table :loading="isOfflining" :columns="offlineData.cols" :data="offlineData.list"></i-table>
                </div>
            </div>
        </div>
        <!-- <loading :loading="showLoading"></loading> -->
    </div>
</template>

<script>
export default {
    data() {
        let that = this;
        return {
            page: {
                total: 0,
                pageSize: 10,
                pageNum: 1
            },
            showLoading: false,
            searchDate: new Date().pattern('yyyy-MM-dd'),
            isOfflining: false,
            gameList: [],
            offlineData: {
                show: false,
                header: '',
                list: [],
                cols: [
                    {
                        title: '返水',
                        align: 'center',
                        key: 'backwater'
                    },
                    {
                        title: '状态',
                        align: 'center',
                        key: 'status'
                    },
                    {
                        title: '时间',
                        align: 'center',
                        width: 120,
                        key: 'time'
                    }
                ]
            },
            agentReportData: [],
            forbiddenTime: {
                disabledDate (date) {
                    return date && date.valueOf() >= Date.now();
                }
            },
            isLoading: false,
            isBackWater: false,
            auto: null
        }
    },
    beforeRouteEnter (to, from, next) {
        // ...
        next(vm => {
            vm.auto = function() {
                vm.handleGetData().then(function() {
                    setTimeout(function() {
                        if (vm.auto)
                            vm.auto();
                    }, 30000)
                })
            }
            vm.getGameList().then(function() {
                vm.auto();
            })
        })
    },
    beforeRouteLeave (to, from, next) {
        // ...
        let that = this;

        that.auto = null;
        next();
    },
    methods: {
        getGameList() {
            let that = this;

            return new Promise((resolve, reject) => {
                that.$axios({
                    url: '/api/Merchant/GetGameList'
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.gameList = result.map(item => {
                            return {
                                title: item.GameName,
                                type: item.GameType,
                                key: item.NickName
                            }
                        }).sort((a, b) => b.type - a.type);
                        that.gameList.forEach(item => {
                            that.offlineData.cols.unshift({
                                title: item.title,
                                align: 'center',
                                key: item.key
                            })
                        })
                        that.offlineData.cols.unshift({
                            title: '下线昵称',
                            align: 'center',
                            key: 'offlineName'
                        })
                        resolve();
                    }
                    reject();
                })
            })
        },
        handleChangeSize(_pageSize) {
            let that = this;

            that.page.pageSize = _pageSize;
            that.page.pageNum = 1;
            that.handleGetData();
        },
        handleChangePage(page) {
            let that = this;

            that.page.pageNum = page;
            that.handleGetData();
        },
        handleSearch() {
            let that = this;

            that.handleGetData();
        },
        handleBackwater() {
            let that = this;

            if (!that.isBackWater) {
                that.isBackWater = true;

                that.$axios({
                    url: '/api/Monito/GhostUserReport',
                    params: {
                        time: that.searchDate
                    }
                }).then(data => {
                    that.isBackWater = false;

                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                })
            }
        },
        handleQueryOffline(item) {
            let that = this;
            that.offlineData.header = '下线列表';
            that.handlegetOfflineData(item);
            that.offlineData.show = true;
        },
        handleCloseModal() {
            let that = this;
            that.offlineData.show = false;
        },
        handlegetOfflineData(item) {
            let that = this;

            if (!that.searchDate) {
                that.$Message.error('请选择查询日期');
                return;
            }

            if (!that.isOfflining) {
                that.isOfflining = true;
                that.offlineData.list = [];

                that.$axios({
                    url: '/api/Monito/GetOfflineReport',
                    params: {
                        time: that.searchDate,
                        agentID: item.agentId
                    }
                }).then(data => {
                    that.isOfflining = false;

                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.offlineData.list = [];
                        result.forEach(item => {
                            let res = {
                                offlineName: item.NickName + '(' + item.OnlyCode + ')',
                                showId: item.OnlyCode,
                                backwater: item.Ascent,
                                status: item.BackStatus === 1 ? '已回水' : '未回水',
                                time: item.Time
                            };
                            that.gameList.forEach(ele => {
                                res[ele.key] = item[ele.key];
                            })

                            that.offlineData.list.push(res);
                        })
                    }
                })
            }
        },
        handleGetData() {
            let that = this,
                param = {};

            return new Promise((resolve, reject) => {
                param['start'] = that.page.pageNum;
                param['pageSize'] = that.page.pageSize;

                if (!that.searchDate) {
                    that.$Message.error('请选择查询日期');
                    return;
                } else {
                    param['time'] = that.searchDate;
                }

                if (!that.isLoading) {
                    that.isLoading = true;

                    that.$axios({
                        url: '/api/Monito/SearchAgentReport',
                        params: param
                    }).then(data => {
                        that.isLoading = false;
                        if (data.data.Status === 100) {
                            const result = data.data.Data;

                            that.page.total = data.data.Total;
                            that.agentReportData = [];
                            result.forEach(item => {
                                let allScroll = 0;
                                let res = {
                                    agentName: item.NickName + '(' + item.OnlyCode + ')',
                                    showId: item.OnlyCode,
                                    backwater: item.Ascent,
                                    status: item.BackStatus === 1 ? '已回水' : '未回水',
                                    agentId: item.AgentID
                                };

                                that.gameList.forEach(ele => {
                                    allScroll += item[ele.key];
                                })

                                res['allScroll'] = allScroll;
                                that.agentReportData.push(res);
                            })
                        }
                        resolve();
                    })
                }
            })
        }
    }
}
</script>

<style lang="scss" scoped>
.agent-report {
    position: relative;
    .content {
        .list-header {
            .list-title {
                justify-content: space-between;

                width: 100%;
                .list-operate {
                    display: flex;
                    align-items: center;

                    font-weight: 400;

                    .columns-item-input {
                        width: 140px;
                    }

                    .search {
                        margin-left: 13px;

                        width: 106px;
                        height: 28px;
                        line-height: 24px;

                        // background-color: rgb(255, 255, 255);
                        // border-radius: 4px;
                        // border: solid 1px rgb(102, 102, 102);
                        background-color: #5b55d9;
                        box-shadow: inset 0px 0px 5px 0px rgba(127, 124, 255, 0.75);
                        border-radius: 10px;
                        border: solid 1px #0e2390;
                        text-align: center;
                        cursor: pointer;
                        color: #fff;
                    }

                    .poptip {
                        flex-shrink: 0;

                        margin-left: 60px;
                        margin-right: 15px;

                        width: 460px;

                        color: rgb(102, 102, 102);

                        & > p {
                            line-height: 14px;
                        }

                        & > p:last-child {
                            margin-top: 5px;
                        }
                    }
                }
            }
        }

        .list-data {
            margin-top: 20px;

            border: solid 1px #bcbcbc;

            .data-header, .data-item {
                display: flex;
                align-items: stretch;

                height: 50px;

                background-color: #e4e4e4;

                .item-info {
                    flex: 1 1 24.5%;
                    line-height: 50px;

                    border-right: solid 1px #d7d7d7;

                    & > img {
                        width: 36px;
                        height: 36px;

                        vertical-align: middle;
                    }
                }

                .item-info:nth-child(6) {
                    border-right: 0;
                }

                .operate {
                    display: flex;
                    align-items: center;
                    justify-content: space-around;

                    .item-button {
                        width: 108px;
                        height: 27px;
                        line-height: 23px;

                        // background-color: rgb(255, 255, 255);
                        // border-radius: 4px;
                        // border: solid 1px rgb(102, 102, 102);
                        background-color: #0078ff;
                        box-shadow: inset 0px 0px 5px 0px rgba(124, 183, 255, 0.75);
                        border-radius: 10px;
                        border: solid 1px #0e3c90;
                        cursor: pointer;
                        color: #fff;
                    }
                }
            }

            .data-con {
                min-height: 580px;

                overflow: auto;
                .data-item {
                    height: 40px;

                    background-color: #fff;
                    .item-info {
                        line-height: 40px;

                    }
                }

                .data-item:nth-child(2n+2) {
                    background-color: #f9f9f9;
                }
            }
        }
    }

    .offline-data-mask {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;

        width: 100%;
        height: 100%;

        background-color: rgba(55,55,55,.6);

        z-index: 1000;

        .offline-data {
            position: fixed;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;

            margin: auto;

            width: 1200px;
            height: 600px;

            border: solid 3px #cdcdcd;
            border-radius: 4px;

            background-color: #f2f2f2;

            z-index: 1001;

            .offline-data-header {
                display: flex;
                justify-content: space-between;

                padding: 5px;

                background-color: #ddd;

                font-weight: bold;

                .modal-close {
                    cursor: pointer;
                }
            }

            .offline-data-list {
                margin: 10px;

                border: solid 1px #ddd;
                border-radius: 4px;
            }
        }
    }
}
</style>